@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, h1, h2, h3, h4, h5, h6, div, img, p, a, input, span, em, ul, ol, li{
    margin: 0;
    padding: 0;
}

body{
    box-sizing: border-box;
}
ul, li {
    list-style: none;
}
body{
    background-color: rgb(48, 0, 0);
}
.App{
    position: relative;
}
.while-loading{
    cursor: wait;
}
.loading{
    width: 100vw;
    margin: 0 auto;
    height: 100vh;
    background-color: hsl(0, 50%, 20%);
}
.loading h1{
    height: 100vh;
    line-height: 100vh;
    font-size: 5rem;
    text-align: center;
    color: #ffffff;
}
/* movie-detail */
.movie-detail {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color:rgba(0,0,0,0.5);
    z-index: 1;
    display: none;
}
.movie-detail .container {
    position: relative;
}

.movie-detail-cancel {
    position: absolute;
    top: 0px;
    right: 100px;
    color: rgb(182, 182, 182);
    font-size: 2rem;
    background-color: transparent;
    outline: none;
    border : none;
    cursor: pointer;
}
.movie-detail-cancel:hover {
    color: rgb(253, 253, 253);
}
.movie-detail-box{
    display: flex;
    flex-direction: row;
    width: 900px;
    height: 600px;
    background-color: #fff;
    margin: 0 auto;
    margin-top: 50px;
    box-shadow: 0 0 20px rgba(255,255,255,0.5);
    border-radius: 100px;
}
.movie-detail-poster{
    margin-top: 80px;
    background-color: hsl(0, 50%, 20%);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 15px 10px;
}
.movie-detail-poster img {
    width: 250px;
}
.md-title {
    text-align: center;
    font-size: 1.5rem;
    font-family: 'Oswald', sans-serif;
    color: rgb(255, 238, 238);
}
.movie-detail-info {
    margin-top: 30px;
    margin-right: 30px;
}
.md-info-rating{
    font-family: 'Exo', sans-serif;
    font-size: 1.2rem;
}
.movie-detail-info p {
    margin: 20px 0;
    padding: 10px 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 400px; 
    background-color: rgba(0,0,0,0.1);
    border : 2px solid hsl(0, 50%, 20%);
    border-radius: 20px;
    overflow: scroll;
}
.movie-detail-info p::-webkit-scrollbar{
    display: none;
}
.movie-detail-info ul {
    font-family: 'Exo', sans-serif;
    font-size: 1.1rem;
}
.movie-detail-info ul li {
    display: inline-block;
    margin: 0 10px;
}
@media (max-width : 1280px){
    .loading h1{
        font-size: 4rem;
    }
    .movie-detail-cancel{
        right: 50px;
    }
    .movie-detail-box{
        width: 800px;
        height: 500px;
    }
    .movie-detail-poster {
        margin-top: 50px;
    }
    .movie-detail-poster img {
        width: 200px;
    }
    .md-title{
        font-size: 1.3rem;
    }
    .movie-detail-info p {
        height: 300px;
    }
}
@media (max-width : 960px){
    .loading h1{
        font-size: 3rem;
    }
    .movie-detail-cancel{
        right: 70px;
        top: 10px;
    }
    .movie-detail-cancel:hover{
        color: hsl(0, 50%, 20%);
    }
    .movie-detail-box{
        width: 720px;
        height: 500px;
    }
}
@media (max-width : 720px){
    .loading h1{
        font-size: 2.5rem;
    }
    .movie-detail-cancel{
        right: 50px;
    }
    .movie-detail-box{
        width: 480px;
        height: 360px;
    }
    .movie-detail-poster {
        margin-top: 50px;
        padding: 10px 5px;
    }
    .movie-detail-poster img {
        width: 100px;
    }
    .md-title{
        font-size: 0.9rem;
    }
    .md-info-rating{
        font-size: 1rem;
    }
    .movie-detail-info p {
        height: 150px;
    }
    .movie-detail-info ul {
        font-size: 0.9rem;
    }
}
@media (max-width : 480px){
    .loading h1{
        font-size: 2rem;
    }
    .movie-detail-cancel{
        right: 60px;
    }
    .movie-detail-box{
        width: 360px;
        height: 300px;
    }
    .movie-detail-poster {
        margin-top: 50px;
        padding: 5px;
    }
    .movie-detail-poster img {
        width: 100px;
    }
    .movie-detail-info {
        margin-top: 20px;
        margin-right: 20px;
    }
    .md-title{
        font-size: 0.9rem;
    }
    .md-info-rating{
        font-size: 0.8rem;
    }
    .movie-detail-info p {
        margin: 10px 0;
        height: 120px;
        padding: 10px;
        font-size: 0.8rem;
    }
    .movie-detail-info ul {
        font-size: 0.9rem;
    }
}
.container{
    max-width: 1280px;
    margin: 0 auto;
}

.mb-header-title{
    text-align: center;
    font-size: 5rem;
    font-family: 'Oswald', sans-serif;
    color: rgb(255, 238, 238);
}
.mb-header ul {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
.mb-header ul li {
    display: block;
    color: rgb(255, 211, 211);
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 10px;
    font-size: 0.9rem;
}
.mb-header ul li:hover {
    color: hsl(0, 50%, 20%);
    background-color: rgb(255, 211, 211);
}
.movie-main{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.mb-movie{
    width: 600px;
    height: 400px;
    margin: 50px 20px;
    background-color: rgb(202, 202, 202);
    display: flex;
    font-display: row;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(255,255,255,0.7);
}
.mb-movie-poster{
    margin-top: 20px;
    margin-left: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
}
.mb-movie-poster img {
    width: 200px;
    border : 10px solid rgb(37, 37, 37);
    transition : border-color ease 0.2s;
    display: block;
}
.mb-movie-poster img:hover{
    cursor: pointer;
    border-color: hsl(0, 50%, 20%);
}
.mb-movie-id{
    display: none;
}
.mb-movie-info {
    padding: 20px 30px;
}
.mb-movie-info h2 {
    font-family: 'EB Garamond', serif;
}
.mb-movie-info span {
    font-family: 'Exo', sans-serif;
    font-weight: bold;
    display: block;
    text-align: right;
    margin: 10px 0;
}
.mb-movie-info ul {
    margin-top: 10px;
    font-family: 'Exo', sans-serif;
}
.mb-movie-info ul li {
    display: inline-block;
    margin: 0 5px;
}
.mb-movie-info p {
    overflow: scroll;
    height: 200px;
    padding: 10px 20px;
    background-color: rgba(255,255,255,0.3);
    border-radius: 10px;
}
.mb-movie-info p::-webkit-scrollbar{
    display: none;
}
@media (max-width : 1280px){
    .container{
        max-width: 960px;
    }
    .mb-movie-info h2 {
        font-size: 1.3rem;
    }
    .mb-movie {
        width: 430px;
        height: 350px;
    }
    .mb-movie-poster img {
        width: 150px;
    }
    .mb-movie-info span {
        margin: 5px 0;
    }
    .mb-movie-info {
        padding: 20px;
    }
    .mb-movie-info p {
        height: 150px;
    }
}
@media(max-width : 960px){
    .container{
        max-width: 720px;
    }
    .mb-movie {
        width: 300px;
        height: 300px;
    }
    .mb-movie-poster img {
        width: 100px;
        margin-top: 40px;
    }
    .mb-movie-info {
        padding: 10px;
    }
    .mb-movie-info h2 {
        font-size: 1rem;
    }
    .mb-movie-info span {
        font-size: 0.8rem;
    }
    .mb-movie-info p {
        height: 110px;
        font-size: 0.8rem;
    }
    .mb-movie-info ul {
        font-size: 0.8rem;
    }
}
@media (max-width : 720px){
    .container{
        max-width: 480px;
    }
    .mb-movie {
        width: 480px;
        height: 400px;
        margin: 25px 0;
    }
    .mb-movie-poster img {
        width: 150px;
        margin-top: 20px;
    }
    .mb-movie-info {
        padding: 20px;
    }
    .mb-movie-info h2 {
        font-size: 1.3rem;
    }
    .mb-movie-info span{
        font-size: 1rem;
    }
    .mb-movie-info p{
        height: 170px;
        font-size: 1rem;
    }
    .mb-movie-info ul {
        font-size: 0.9rem;
    }
}
@media (max-width : 480px){
    .container{
        max-width: 360px;
    }
    .mb-movie {
        width: 360px;
        height: 300px;
        margin: 20px 0;
    }
    .mb-movie-poster img {
        width: 100px;
        margin-top: 40px;
    }
    .mb-movie-info {
        padding: 10px 20px;
    }
    .mb-movie-info h2 {
        font-size: 1.1rem;
    }
    .mb-movie-info span{
        font-size: 0.9rem;
    }
    .mb-movie-info p{
        height: 120px;
        font-size: 0.9rem;
    }
    .mb-movie-info ul {
        font-size: 0.8rem;
    }
}
.pages {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.pages li {
    color: #fff;
    cursor: pointer;
    margin: 0 2px;
    padding: 0 5px;
}
.pages li.active {
    font-weight: bold;
    border-bottom: 2px solid white;
}
